.cv-title{
  padding: 51px 0 0 0;
  margin-top: 65px;
  position: relative;
  color: white;
  text-align: center;
  font-size: 1.3rem;
  font-family: Roboto, serif;
}
.cv-title:before {
  content: '';
  position: absolute;
  top: 85px;
  left: 48%;
  width: 53px;
  height: 3px;
  background: #ff0000;
}


